// Footer

#footer {
    background: url('../img/photo-footer.jpg') left top / auto 100% no-repeat, url('../img/photo-footer-fill.jpg') left top / auto 100% repeat-x;
    padding: 1rem 0;
    @include bp('min-width: 20em') {
        padding: 1rem; }
    @include bp('max-width: 1190px') {
        background: url('../img/photo-footer-fill.jpg') left top / auto 100% repeat-x;
        [class*="col-2"] {
            width: 20%;
            &:first-child {
                display: none; } }
        [class*="col-4"] {
            width: 40%; }
        .bottom {
            [class*="col-6"] {
                width: 80%; }
            [class*="col-4"] {
                width: 20%; } } }
    @include bp('max-width: 890px') {
        [class*="col-2"] {
            width: calc(100% / 3); }
        [class*="col-4"] {
            display: none; }
        .bottom {
            [class*="col-4"] {
                display: block; } } }
    @include bp('max-width: 550px') {
        .bottom {
            text-align: center;
            [class*="col-6"],
            [class*="col-4"] {
                width: 100%; }
            .social {
                float: none;
                .list {
                    margin: 0 auto; } } } }
    .title {
        @include font-size(16px);
        color: $gray;
        font-weight: 300;
        margin-bottom: 1rem;
        text-transform: uppercase; }
    .list {
        list-style: none;
        .item {
            margin-bottom: .8rem;
            .link {
                @include font-size(14px);
                color: $green;
                text-decoration: none;
                &:hover {
                    color: $blue;
                    text-decoration: underline; } } } }
    .copyright {
        color: $gray; }
    .social {
        .list {
            @include clear;
            .item {
                float: left;
                margin-bottom: 0;
                margin-right: .5rem;
                &:last-child {
                    margin: 0; } } } }
    .newsletter_form {
        margin-bottom: 1rem;
        .input-wrapper {
            margin-top: 1rem; } }
    .newsletter_payments {
        .payments {
            margin-top: 1rem;
            .payment {
                &:last-child {
                    margin: 0; } } } }
    .bottom {
        border-top: 1px solid $border; } }
