// Featured Products

#featured-products {
    padding: 1rem 0;
    @include bp('max-width: 800px') {
        padding: 0 1rem;
        [class*="col-3"] {
            width: calc(100% / 3);
            &:last-child {
                display: none; } } }
    @include bp('max-width: 670px') {
        [class*="col-3"] {
            width: 50%;
            &:last-child {
                display: block; } } }
    .section-title {
        @include font-size(20px);
        color: $gray;
        font-weight: 300; } }
