// Modal

.layer {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($white, 0.9);
    z-index: 99; }
.modal {
    display: none;
    position: fixed;
    z-index: 100;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    background: $white;
    box-shadow: 0 4px 8px 0 rgba($black, .15);
    border-radius: rem(3px);
    overflow: hidden;
    .title {
        background: $green;
        padding: 1rem;
        color: $white;
        position: relative;
        .close-modal {
            @include font-size(26px);
            appearance: none;
            background: none;
            border: 0;
            color: $white;
            position: absolute;
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
            &:hover {
                cursor: pointer; } } }
    .content {
        padding: 1rem; } }
