// Single Product

#single-product {
    padding: 1rem 0;
    .header {
        .breadcrumbs {
            @include font-size(16px);
            color: $gray;
            .link {
                color: $green;
                text-decoration: none;
                &:hover {
                    color: $blue;
                    text-decoration: underline; } } }
        .report {
            .link {
                @include font-size(13px);
                color: $gray;
                text-decoration: none;
                &:hover {
                    color: $red;
                    text-decoration: underline; } } } }
    .single {
        margin-bottom: 3rem;
        .gallery {
            .video-wrapper {
                position: relative;
                padding-bottom: 56.25%;
                padding-top: 25px;
                height: 0;
                iframe {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%; } }
            .featured {
                border-radius: rem(3px);
                width: 100%; } }
        .thumbnails {
            .list {
                list-style: none;
                .item {
                    background: $slider;
                    border-radius: rem(2px);
                    margin-bottom: 1rem;
                    overflow: hidden;
                    position: relative;
                    .img {
                        width: 100%; }
                    &.active,
                    &:hover {
                        .img {
                            @include opacity(.5);
                            cursor: pointer; }
                        &::before {
                            background: $green;
                            bottom: .5rem;
                            content: ' ';
                            height: rem(2px);
                            left: 50%;
                            position: absolute;
                            transform: translateX(-50%);
                            width: 80%; } } } } }
        .information {
            .title {
                @include font-size(28px);
                color: $title;
                font-weight: 300;
                margin: 0;
                margin-bottom: .8rem; }
            .subtitle {
                @include font-size(14px);
                color: $gray;
                font-weight: 300;
                margin-bottom: 1rem; }
            .description {
                @include font-size(16px);
                color: $text;
                line-height: 1.5em;
                margin-bottom: 2rem; }
            .variations {
                margin-bottom: 2rem;
                .variation {
                    display: inline-block;
                    margin-right: 1rem;
                    &:last-child {
                        margin-right: 0; }
                    .title {
                        @include font-size(16px);
                        color: $title;
                        margin-bottom: .5rem; }
                    .select {
                        @include font-size(18px);
                        appearance: none;
                        background: url('../img/caret-down.svg') right 1rem center no-repeat, $white;
                        border: 1px solid $border;
                        border-radius: rem(2px);
                        color: $text;
                        padding: .8rem 2rem .8rem 1rem; } } }
            .add-to-cart {
                .quantity {
                    margin-bottom: 1rem;
                    .title {
                        @include font-size(16px);
                        color: $title;
                        margin-bottom: .5rem; }
                    .control {
                        @include font-size(28px);
                        appearance: none;
                        background: none;
                        border: 0;
                        color: $green;
                        font-family: $sans; }
                    .qty {
                        @include font-size(16px);
                        appearance: none;
                        background: $white;
                        border: 1px solid $border;
                        color: $green;
                        font-family: $sans;
                        padding: .8rem 1rem;
                        text-align: center; } }
                .btn {
                    @include font-size(16px);
                    width: 100%; } } } } }
.characteristics {
    margin-bottom: 2rem;
    .title {
        @include font-size(20px);
        color: $green;
        font-weight: 300;
        margin-bottom: 2rem;
        text-transform: uppercase; }
    .list {
        .item {
            margin-bottom: 2rem;
            &:last-child {
                margin-bottom: 0; }
            .name {
                @include font-size(16px);
                color: $green; }
            .description {
                color: $text;
                margin-top: .5rem;
                padding-left: rem(30px); } } } }
.reviews,
.questions {
    .empty {
        @include font-size(18px);
        background: $cloud;
        border-radius: rem(3px);
        color: $text;
        padding: 3rem 1rem;
        text-align: center; }
    .sign-in {
        @include font-size(18px);
        background: $cloud;
        border-radius: rem(3px);
        color: $text;
        padding: 3rem 1rem;
        text-align: center;
        margin-bottom: 1rem;
        .link {
            color: $green;
            text-decoration: none;
            &:hover {
                color: $blue;
                text-decoration: underline; } } } }
.review-form,
.question-form {
    margin-bottom: 2rem;
    .input-wrapper {
        background: $white;
        border: 1px solid $border;
        border-radius: rem(2px);
        display: inline-block;
        overflow: hidden;
        .input {
            @include font-size(14px);
            background: none;
            border: 0;
            color: $text;
            font-family: $sans;
            padding: .7rem .8rem;
            width: rem(400px); }
        .btn {
            border-radius: 0; } } }
.reviews {
    .title {
        @include font-size(20px);
        color: $green;
        font-weight: 300;
        margin-bottom: 1rem;
        text-transform: uppercase; }
    .review {
        background: $white;
        border-radius: rem(3px);
        color: $text;
        margin-top: 2rem;
        line-height: 1.5;
        padding: 1rem 1.5rem;
        .header {
            margin-bottom: 1rem;
            .rating {
                color: $title;
                margin-bottom: .3rem;
                .stars-list {
                    display: inline-block; } }
            .variation {
                color: $gray; } }
        .link {
            color: $green;
            text-decoration: none;
            &:hover {
                color: $blue;
                text-decoration: underline; } } }
    .review-form {
        .rating-radios {
            padding: .5rem 0;
            .star {
                &:hover {
                    cursor: pointer; }
                .radio {
                    display: none; } } } } }
.questions {
    .title {
        @include font-size(20px);
        color: $green;
        font-weight: 300;
        margin-bottom: 1rem;
        text-transform: uppercase; }
    .subtitle {
        @include font-size(13px);
        color: $gray;
        font-weight: 300;
        margin-bottom: 1rem; }
    .conversation {
        .thread {
            background: $white;
            border-radius: rem(3px);
            margin-bottom: 1.5rem;
            padding: 1.5rem;
            .question {
                float: right;
                margin-bottom: 1rem;
                text-align: right;
                .header {
                    @include font-size(13px);
                    color: $text;
                    margin-bottom: .2rem;
                    .link {
                        color: $green;
                        text-decoration: none;
                        &:hover {
                            color: $blue;
                            text-decoration: underline; } } }
                .content {
                    background: $green;
                    border-radius: rem(30px);
                    color: $white;
                    display: inline-block;
                    padding: .8rem 1rem; } }
            .answer {
                float: left;
                .header {
                    @include font-size(13px);
                    color: $text;
                    margin-bottom: .2rem;
                    .link {
                        color: $green;
                        text-decoration: none;
                        &:hover {
                            color: $blue;
                            text-decoration: underline; } } }
                .content {
                    background: $blue;
                    border-radius: rem(30px);
                    color: $white;
                    display: inline-block;
                    padding: .8rem 1rem; } } } } }
.related_products {
    .title {
        @include font-size(20px);
        color: $title;
        font-weight: 300;
        text-transform: uppercase; } }
